import React from 'react';
import { connect } from 'react-redux';
import { getParameterByName } from '../util';
import * as actions from '../actions';
import { FiMenu } from 'react-icons/fi';
import { FiSave } from 'react-icons/fi';
import { Checkbox } from 'primereact/checkbox';

function Navbar(props) {
	const { toggleSidebar, toggleViewOnly, ui } = props;
	const { asset, assetDef } = props.data;
	const viewOnly = ui.viewOnly ? 'hidden' : ''
	const show = window.innerWidth > 600 ? 'block' : 'none';
	const name = asset && assetDef ?
	 getParameterByName('mode') === 'builder' ? assetDef.assetDefinitionName : asset.assetAlias
	 : '';

	function handleSave() {
		const {tiles, layouts, tabs} = props;
		props.saveDashboard(tiles, layouts, tabs)
		.then(() => alert('Dashboard has been saved!'));
	}

	return (
		<nav className="navbar">
			<div
				className='navbar-menu navbar-section'
				style={{cursor:'pointer', visibility: viewOnly}}
				onClick={toggleSidebar}
			>
				<FiMenu style={{height:'1.5rem'}} title='open menu' className='navbar-menu-icon' size={'1.5rem'} />
			</div>
			<h1
				style={{margin: 0, marginLeft: '1rem', visibility: viewOnly, cursor: 'pointer'}}
				className="navbar-asset"
				title='Save Dashboard'
				onClick={handleSave}
			>
					<FiSave style={{height:'1.5rem'}} className='navbar-menu-icon' size={'1.5rem'} />
			</h1>
			<div className='navbar-section'>
				<Checkbox
					style={{marginTop:'.2rem', marginLeft:'1rem', borderRadius: '4px', backgroundColor:'#444 '}}
					title='toggle view'
					className=''
					name='viewOnly'
					checked={ui.viewOnly}
					onChange={toggleViewOnly}
				/>
				<label
					style={{color:'white', fontSize: '.75rem', marginLeft:'.5rem', }}
					title='toggle view'
					htmlFor="viewOnly"
					className="p-checkbox-label navbar-checkbox-label"
				>View only
				</label>
			</div>

			<h1 style={{display: show}} className="navbar-brand navbar-section">{`${name}`}</h1>
			<div  className='navbar-section' style={{marginLeft:'auto', display: show}}>
				<ul style={{listStyle: 'none', margin: 0}}>
					<li className='navbar-navitem'><a href='../assets/list.shtml'>Assets</a></li>
					<li className='navbar-navitem'><a href='../assetdefinitions/index.shtml'>Asset Definitions</a></li>
				</ul>
			</div>
		</nav>
	)
}

function mapStateToProps({ ui, data, tiles, layouts, tabs }) {
	return { ui, data, tiles, layouts, tabs}
}

export default connect(mapStateToProps,actions)(Navbar);
