import React, { Component } from 'react';
import { connect } from 'react-redux';
import { format, _get } from '../../util';

class TextBox extends Component {
  static defaultProps = {
    data: undefined //{}
  }

  substitutions = (text) => {
    const { currentData, data } = this.props;
    if(!text) return 'I am a Text Box'
    console.log(text);
    try {
      return format(text, { currentData, data });
      //return eval('`'+text+'`');
    } catch(e) {
      return text;
    }
  }

  render () {
    const { properties } = this.props.tile;
    const { text } = properties;
    return (
      <section style={{width: '100%', height: '100%', position: 'relative'}} >
        <div dangerouslySetInnerHTML={{__html: this.substitutions(text)}}>
        </div>
      </section>
    )
  }
}

function mapStateToProps({ data, currentData }) {
	return { data, currentData }
}

export default connect(mapStateToProps)(TextBox);
