import React, { Component } from 'react';
import { connect } from 'react-redux';
import Iframe from 'react-iframe';
import { format } from '../../util';

class IframeTile extends Component {
  render () {
    const { data, currentData } = this.props;
    const { id, properties } = this.props.tile;
		return (
			<div
				id={id}
				style={{width:'96%', height: '96%', margin: 'auto', marginTop: '1.5rem', paddingBottom: '1.5rem', borderRadius: '5px'}}
			>
        <Iframe
          url={format(properties.url, { data, currentData })}
          style={{borderRadius: '5px'}}
          width="100%"
          height="100%"
          className=""
          display="initial"
          position="relative"
          allowFullScreen
        />
			</div>
		)
  }
}

function mapStateToProps({ data, currentData }) {
	return { data, currentData }
}

export default connect(mapStateToProps)(IframeTile);
