import axios from 'axios';
import { keys } from './keys';

export const guid = () => {
  function s4() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }
  return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
}

export const getParameterByName = ( name ) => {
  name = name.replace(/[\[]/,"\\\[").replace(/[\]]/,"\\\]");
  var regexS = "[\\?&]"+name+"=([^&#]*)";
  var regex = new RegExp( regexS );
  var results = regex.exec( window.location.href );
  if( results == null )
    return "";
  else
    return decodeURIComponent(results[1].replace(/\+/g, " "));
}

export const isEmpty = (obj) => {
  return (Object.keys(obj).length === 0 && obj.constructor === Object);
}

// regex replacement functions
export function format(formatString, replacer){
  try {
    let replacedString = (' ' + formatString).slice(1);
  	const regex = /{{(.*?)}}/g
    var matches = formatString.matchAll(regex);
    for (let match of matches) {
      const replacement = _get(replacer, match[1], match[1]);
      replacedString = replacedString.replace(match[0],replacement);
    }
    return replacedString;
} catch(e) {
  return formatString
}
}

export function _get(obj, path, def) {
	/**
	 * If the path is a string, convert it to an array
	 * @param  {String|Array} path The path
	 * @return {Array}             The path array
	 */
	const stringToPath = function (path) {
		// If the path isn't a string, return it
		if (typeof path !== 'string') return path;
		// Create new array
		const output = [];
		// Split to an array with dot notation
		path.split('.').forEach(function (item, index) {
			// Split to an array with bracket notation
			item.split(/\[([^}]+)\]/g).forEach(function (key) {
				// Push to the new array
				if (key.length > 0) {
					output.push(key);
				}
			});
		});
		return output;
	};
	// Get the path as an array
	path = stringToPath(path);
	// Cache the current object
	let current = obj;
	// For each item in the path, dig into the object
	for (var i = 0; i < path.length; i++) {
		// If the item isn't found, return the default (or null)
		if (!current[path[i]]) return def;
		// Otherwise, update the current  value
		current = current[path[i]];
	}
	return current;
};




export const loginStatus = async () => {
  if(!window.location.href.toLowerCase().match("login")) {
    sessionStorage.destination = window.location.href;
    const authParam = getParameterByName('authJWT');
    if(authParam) {
      document.cookie = `authJWT=${authParam}`;
    }
    try {
      const request = await axios({
        url: '/api/Auth/Status',
        baseURL: keys.BASE_URL,
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
        }
      });
      const status = request.data;
      if(status.login === 'valid') {
        sessionStorage.destination = window.location.href
        //window.location.replace('?error='+data.error.source)
      } else {
        window.location.href = '/login';
      }
    } catch {
      window.location.href = '/login';
    }
  }
}

